.section {
    padding: var(--space-8) var(--space-6);
    max-width: 1280px;
    margin: 0 auto;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: var(--space-4);
}

.titleWrapper {
    display: flex;
    align-items: center;
    gap: var(--space-2);
}

.fireIcon {
    color: #f97316;
}

.title {
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
}

.controls {
    display: flex;
    gap: var(--space-2);
}

.navBtn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-dark-tertiary);
    border: 1px solid var(--border-subtle);
    border-radius: 50%;
    color: var(--text-secondary);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.navBtn:hover {
    background: var(--bg-card-hover);
    color: var(--text-primary);
}

.sliderWrapper {
    position: relative;
    margin: 0 calc(var(--space-6) * -1);
    padding: 0 var(--space-6);
}

.slider {
    display: flex;
    gap: var(--space-3);
    overflow-x: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    padding: var(--space-2) 0;
}

.slider::-webkit-scrollbar {
    display: none;
}

.card {
    flex: 0 0 140px;
    height: 90px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: var(--space-3);
    border-radius: var(--radius-xl);
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border: none;
    text-align: left;
}

.card::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(180deg, transparent 30%, rgba(0, 0, 0, 0.4) 100%);
}

.hotBadge {
    position: absolute;
    top: var(--space-2);
    left: var(--space-2);
    padding: 2px var(--space-2);
    background: rgba(0, 0, 0, 0.4);
    border-radius: var(--radius-sm);
    font-size: 0.625rem;
    font-weight: 600;
    color: white;
}

.cardTitle {
    position: relative;
    z-index: 1;
    font-size: 0.8125rem;
    font-weight: 600;
    color: white;
    line-height: 1.3;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.cardSubtitle {
    position: relative;
    z-index: 1;
    font-size: 0.6875rem;
    color: rgba(255, 255, 255, 0.8);
    margin-top: 2px;
}

@media (max-width: 768px) {
    .section {
        padding: var(--space-6) var(--space-4);
    }

    .sliderWrapper {
        margin: 0 calc(var(--space-4) * -1);
        padding: 0 var(--space-4);
    }

    .card {
        flex: 0 0 120px;
        height: 80px;
    }
}